.form {
  width: 403px;
  margin: 0;
  padding-top: 44px;
  padding-right: 49px;
  padding-bottom: 50px;
  padding-left: 50px;

  @media (max-width: $breakpoint-mobile) {
    width: 290px;
    padding-top: 35px;
    padding-right: 30px;
    padding-bottom: 40px;
    padding-left: 30px;
  }
}

.form__text {
  margin-top: 0;
  margin-bottom: 27px;
  min-width: 303px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  display: flex;
  align-items: center;
  color: $color-white;

  @media (max-width: $breakpoint-mobile) {
    text-align: center;
    line-height: 25px;
    min-width: 214px;
    margin-bottom: 28px;
  }
}

.form__button {
  margin-top: 29px;
  // position: relative;
  display: block;
  width: 303px;
  padding: 11px 90px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: $color;
  background-color: $color-white;
  border: none;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $breakpoint-mobile) {
    margin-top: 25px;
    width: 230px;
    padding: 11px 53px;
  }
}

.form__input {
  box-sizing: border-box;
  overflow: hidden;
  width: 100%;
  height: 43px;
  padding-top: 16px;
  padding-right: 25px;
  padding-bottom: 16px;
  padding-left: 16px;

  font-family: "Montserrat", "Arial", sans-serif;
  font-style: 400;
  font-size: 14px;
  // line-height: 21px;
  line-height: 100%;
  border: none;
  border-radius: 0;
}

.form input::-webkit-input-placeholder {
  color: $color;
  opacity: 1;
}

.form input::-moz-placeholder {
  color: $color;
  opacity: 1;
}

.form input:-ms-input-placeholder {
  color: $color;
  opacity: 1;
}

.form input::-ms-input-placeholder {
  color: $color;
  opacity: 1;
}

.form input::placeholder {
  color: $color;
  opacity: 1;
}

