.form__checkbox-label {
  position: relative;
  display: block;
  padding: 0 0 0 24px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: $color-white;
  cursor: pointer;

  &::before {
    content: " ";
    position: absolute;
    left: 0;
    top: -2px;
    z-index: 7;

    width: 18px;
    height: 18px;

    border: none;
    background-color: $color-white;
    cursor: pointer;

    @media (max-width: $breakpoint-mobile) {
      top: 2px;
      width: 20px;
      height: 20px;
    }
  }

  &:focus {
    outline: 3px solid #4d90fe;
    outline-offset: 10px;
    outline-style: auto;
  }

  @media (max-width: $breakpoint-mobile) {
    line-height: 12px;
    padding-left: 28px;
  }
}

