.form__checkbox:checked + label::before {
  background-image: url("../img/check-form.svg");
  background-position: 2px 3px;
  background-repeat: no-repeat;
  background-size: 14px 13px;
}

.form__checkbox:hover + label::before {
  border-color: $color;
}

.form__paragraph {
  margin-top: 0;
  margin-bottom: 20px;

  &--email {
    margin-bottom: 22px;

    @media (max-width: $breakpoint-mobile) {
      margin-bottom: 18px;
    }
  }

  &--checkbox {
    margin-bottom: 32px;

    @media (max-width: $breakpoint-mobile) {
      margin-bottom: 28px;
    }
  }
}

.form__span {
  @media (max-width: $breakpoint-mobile) {
    display: block;
    // margin-left: 28px;
  }
}
